import React, { useState } from 'react';
import Head from 'next/head';
import { Zap, Shield, Globe, Layout, ChevronRight } from 'lucide-react';
import LoginModal from '../components/LoginModal'; // Import the component we just made

export default function LandingPage() {
  const [showLogin, setShowLogin] = useState(false);

  return (
    <>
      <Head>
        <title>CMS Admin | Modern Content Management</title>
      </Head>

      <div className="min-h-screen bg-[#0B0B0F] text-white overflow-hidden relative selection:bg-purple-500/30">
        
        {/* Background Gradients (Atmosphere) */}
        <div className="absolute top-[-20%] left-[-10%] w-[500px] h-[500px] bg-purple-600/20 rounded-full blur-[120px] pointer-events-none" />
        <div className="absolute bottom-[-20%] right-[-10%] w-[500px] h-[500px] bg-pink-600/10 rounded-full blur-[120px] pointer-events-none" />

        {/* Navigation */}
        <nav className="relative z-10 max-w-7xl mx-auto px-6 py-6 flex justify-between items-center">
          <div className="flex items-center gap-2 font-bold text-xl tracking-tight">
            <div className="w-8 h-8 rounded-lg bg-gradient-to-br from-pink-500 to-purple-600 flex items-center justify-center">
              <Layout size={18} className="text-white" />
            </div>
            <span>Admin<span className="text-purple-400">CMS</span></span>
          </div>
          <button 
             onClick={() => setShowLogin(true)}
             className="text-sm text-gray-400 hover:text-white transition-colors"
          >
            Log In
          </button>
        </nav>

        {/* Hero Section */}
        <main className="relative z-10 flex flex-col items-center justify-center text-center mt-20 px-4">
          
          <div className="inline-flex items-center gap-2 px-3 py-1 rounded-full bg-white/5 border border-white/10 text-xs font-medium text-purple-300 mb-8 hover:bg-white/10 transition-colors cursor-default">
            <span className="w-2 h-2 rounded-full bg-green-500 animate-pulse"></span>
            v2.0 is now live
          </div>

          <h1 className="text-5xl md:text-7xl font-bold tracking-tight mb-6 max-w-4xl">
            Your content, <br />
            <span className="bg-clip-text text-transparent bg-gradient-to-r from-pink-500 via-purple-500 to-indigo-500 animate-gradient">
              your way.
            </span>
          </h1>

          <p className="text-lg text-gray-400 max-w-2xl mb-10 leading-relaxed">
            A powerful, fully customizable content management system built from scratch. 
            Manage your portfolio, blog, and projects with complete control.
          </p>

          <div className="flex flex-col sm:flex-row gap-4 w-full sm:w-auto">
            <button 
              onClick={() => setShowLogin(true)}
              className="px-8 py-4 bg-gradient-to-r from-pink-600 to-purple-600 hover:opacity-90 rounded-full font-bold text-white shadow-[0_0_20px_-5px_rgba(168,85,247,0.5)] transition-all transform hover:-translate-y-1 flex items-center justify-center gap-2"
            >
              Get Started <ChevronRight size={18} />
            </button>
            <button className="px-8 py-4 bg-white/5 border border-white/10 hover:bg-white/10 rounded-full font-bold text-white transition-all">
              View Documentation
            </button>
          </div>

          {/* Feature Grid */}
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mt-24 max-w-6xl w-full px-6 pb-20">
            <FeatureCard 
              icon={<Zap className="text-yellow-400" />}
              title="Lightning Fast"
              desc="Built with Next.js and optimized for performance. Your content loads instantly."
            />
            <FeatureCard 
              icon={<Shield className="text-pink-400" />}
              title="Secure by Design"
              desc="Role-based access control and secure authentication protect your data."
            />
            <FeatureCard 
              icon={<Globe className="text-blue-400" />}
              title="API-First"
              desc="RESTful APIs serve your content anywhere - websites, apps, and more."
            />
          </div>
        </main>

        {/* Modal Logic */}
        {showLogin && <LoginModal onClose={() => setShowLogin(false)} />}
      </div>
    </>
  );
}

// Sub-component for the features (Internal to this file for simplicity)
const FeatureCard = ({ icon, title, desc }) => (
  <div className="p-6 rounded-2xl bg-white/5 border border-white/10 hover:border-purple-500/30 transition-colors text-left group">
    <div className="w-12 h-12 rounded-lg bg-white/5 flex items-center justify-center mb-4 group-hover:scale-110 transition-transform">
      {icon}
    </div>
    <h3 className="text-xl font-bold mb-2 text-white">{title}</h3>
    <p className="text-gray-400 text-sm leading-relaxed">{desc}</p>
  </div>
);